import { Button, Container, Paper, TextField } from '@mui/material'
import { makeStyles } from '@mui/styles'
import React, { useContext, useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import {context} from '../reducer_sec/ContextApi'
// import {withRouter} from 'react-router-dom'

const useStyles = makeStyles(theme=>({
  mainDiv:{
    marginTop:theme.spacing(10),
    // height:'300px'
  },
  regAc:{
    textAlign:'center',
    fontSize:'30px'
  },
  lineOne:{
    textAlign:'center',
    marginTop:theme.spacing(2)

  },
  username:{
    marginBottom:theme.spacing(1)
  },
  btnReg:{
    textAlign:'center',
    marginTop:theme.spacing(3),
    paddingBottom:theme.spacing(3)
  },
  login:{
    textAlign:'right',
    textDecoration:'underline',
    paddingBottom:theme.spacing(4),
    marginRight: theme.spacing(2),
   
  },
  loginSpan:{
    cursor:'pointer',
    color:'blue'
  }
}))

const RegisterPage = () => {
  const navigate = useNavigate()
  const {database,setDataBase} = useContext(context)
    const classes = useStyles()
    const [fname,setFname]=useState('')
    const [pass1,setPass1]=useState('')
    const [pass2,setPass2]=useState('')
    const [email,setEmail]=useState('')
    const [mobile,setMobile]=useState('')
    const [dob,setDob]=useState('')
    const [tick,setTick]=useState(true)
    // const [database,setDataBase]=useState([])
    const [dis,setDis]=useState(true)
    console.log(database)
    useEffect(()=>{
      if(fname && pass1 && pass2 && email && mobile && dob  && database){
        setDis(false)
      }
      else{
        setDis(true)
      }
    })

    const tickHandler =(e)=>{
      if(tick===true){
        setTick(false)
      }
      else if(tick===false){
      setTick(true)
      }
    }

    const nameHandler=(e)=>{
      const store = e.target.value
      setFname(store)
    }
    const pass1Handler=(e)=>{
      const store = e.target.value
      setPass1(store)
    }
    const pass2Handler=(e)=>{
      const store = e.target.value
      setPass2(store)
    }
    const emailHandler=(e)=>{
      const store = e.target.value
      setEmail(store)
    }
    const mobHandler=(e)=>{
      const store = e.target.value
      setMobile(store)
    }
    const dobHandler=(e)=>{
      const store = e.target.value
      setDob(store)
    }
    const submitHandler=(e)=>{
      e.preventDefault()
      if(pass1!==pass2){
        alert('Please Enter Same Passwords in Both Password Fields')
      }
      else{
      setDataBase([{FullName:fname,Password1:pass1,Password2:pass2,Email:email,MobileNumber:mobile,DoB:dob,NeedPromoNotifications:tick},...database])
      }
      navigate('/login')
      // <Redirect to='/login'/>
      setFname('')
      setMobile('')
      setPass1('')
      setPass2('')
      setEmail('')
      setDob('')
      
    }

    // console.log(dis)
  return (
    <Container>
    <Paper elevation={24} className={classes.mainDiv}>
      <div className={classes.regAc}>
        <div>
          <strong>Register Account</strong>
        </div>
      </div>
      <form>
      <div className={classes.lineOne}>
      <div className={classes.username}>
      <TextField
      onChange={nameHandler}
      value={fname}
          id="fullName"
          name="fullname"
          label="Full Name"
          type="text"
        />
        </div>
        <div className={classes.password1}>
        <TextField
        onChange={pass1Handler}
        value={pass1}

          id="pass1"
          name="pass1"
          label="Password"
          type="password"
          autoComplete="current-password"
        />
        </div>
      </div>
      <div className={classes.lineOne}>
      <div className={classes.username}>
      <TextField
      onChange={pass2Handler}
      value={pass2}
          id="pass2"
          name="pass2"
          label="Repeat Password"
          type="password"
        />
        </div>
        <div className={classes.password1}>
        <TextField
        onChange={emailHandler}
        value={email}

          id="outlined-password-input"
          name="email"
          label="E-mail"
          type="email"
        />
        </div>
      </div>
      <div className={classes.lineOne}>
      <div className={classes.username}>
      <TextField
      onChange={mobHandler}
      value={mobile}
          id="mob"
          name="mob"
          label="Mobile No."
          type="text"
        />
        </div>
        <div className={classes.password1}>
        <label htmlFor="">Date of Birth</label><br />
        <TextField
        onChange={dobHandler}
        value={dob}

        name="date"
          id="date"
          type="date"
          sx={{width:'250px'}}
        />
        <div>
        <input onChange={tickHandler} type="checkbox" name="" id=""  checked={tick}/>
        <label htmlFor="">Need Promotonal Message?</label>
        </div>
        </div>
      </div>
      <div className={classes.btnReg}>
        <Button onClick={submitHandler} disabled={dis} type='submit' size='large' variant='contained'>Register</Button>
      </div>
      <div className={classes.login}>
        <small>Existing Customer? <span className={classes.loginSpan} onClick={()=>{navigate('/login')}}>Log In</span></small>
      </div>
      </form>
    </Paper>
    {/* <table border="1px">
      <tr>
        <th>S.N</th>
        <th>Full_Name</th>
        <th>Passwod_1</th>
        <th>Password_2</th>
        <th>Email Id</th>
        <th>Mobile No.</th>
        <th>Date_of_Birth</th>
        <th>Need_Promo_Message</th>
      </tr>
      {database.map((ele,ind)=>{return (
        <tr>
          <th>{ind+1}</th>
          <th>{ele.FullName}</th>
          <th>{ele.Password1}</th>
          <th>{ele.Password2}</th>
          <th>{ele.Email}</th>
          <th>{ele.MobileNumber}</th>
          <th>{ele.DoB}</th>
          <th>{ele.NeedPromoNotifications?'Yes Allowed':'Not Need'}</th>
        </tr>
      )})}
      
    </table> */}
    </Container>
  )
}

export default RegisterPage